/* Generic buttons style */

.primary_bg {
  background-color: #000071 !important;
  border: none !important;
}

.primary_color {
  color: #000071 !important;
}

.primary_outline {
  background-color: transparent !important;
  border: 1px solid #000071 !important;
  color: #000071 !important;
}

.primary_outline:hover {
  background-color: #000071 !important;
  border: 1px solid #000071 !important;
  color: #fff !important;
}

.secondary_bg {
  background-color: #cccccc !important;
  border-color: #cccccc !important;
  color: #000 !important;
}

/* Chat Module */

.chatSection .chat_dropdown span {
  font-size: 16px;
  color: #a3a3a3 !important;
}

.chatSection .borderRight {
  border-right: 1px solid #d3d2d2;
}

.chatSection .userChat {
  cursor: pointer;
}

.chatSection .userChat img {
  width: 60px!important;
  height: 60px !important;
}

.chatSection .chatActive img {
  width: 45px !important;
}

.userChat .color_light {
  color: #a3a3a3;
}

.userChat .chatCount {
  background-color: #000071;
  padding: 0 7px;
}

.chatSection .chatList_margin {
  margin-top: 15px;
}

.chatSection .paddingBottom {
  padding-bottom: 10px;
}

.chatContent .chatSpan {
  box-shadow: 0px 0px 19px rgba(0, 0, 0, 0.1);
  color: #999999 !important;
  padding: 6px 12px;
  font-size: 16px;
  width: 10rem !important;
}

/* .chatContent .chatMessage_left span {
  border-radius: 20px 20px 20px 0px;
  padding: 8px 25px;
  font-size: 16px;
  border: 1px solid #e3e3e3;
} */

.chatContent textarea::-webkit-scrollbar {
  overflow: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  width: 0;
  display: none;
}
.chatContent .chatMessage_right span {
  padding: 10px 30px;
  font-size: 16px;
  background: #000071;
  border-radius: 20px 20px 0px 20px;
  font-weight: 400;
}

.chatContent .chatTime {
  color: #9c9c9c !important;
  font-size: 16px !important;
}

.chatContent .padding {
  padding: 1.3rem 4rem 1.3rem 1.3rem !important;
}

.chatContent .sendIcon {
  position: absolute;
  top: 12px;
  right: 10px;
  background-color: #000071 !important;
  padding: 15px;
}

.chatContent .bg-transparent {
  background-color: transparent;
}

.chatContent .messageSpan {
  word-wrap: break-word !important;
  white-space: normal !important;
  width: fit-content;
  border-radius: 20px 20px 20px 0px;
  padding: 8px 25px;
  font-size: 16px;
  border: 1px solid #e3e3e3;
  max-width: calc(100vh - 8rem);
}

.chatContent .chatBox {
  overflow-y: auto;
}

.chatSection button {
  border: 0 !important;
}

@media screen and (max-width: 1500px) {
  .chatContent .chatBox {
    overflow-y: auto;
    max-height: 1000px;
  }

  /* .chatContent .messageSpan {
    width: 100%;
  } */
}

@media screen and (max-width: 1200px) {
  /* .chatContent .messageSpan {
    width: 100%;
  } */

  .chatContent .messageSpan2 {
    width: 100%;
  }

  .chatSection .borderRight {
    border: 0;
  }
}

@media screen and (max-width: 600px) {
  .chatSection .container-fluid {
    padding: 36px 20px !important;
  }

  .userChat .mobileTextHeading {
    font-size: 16px;
  }

  .userChat .mobileText {
    font-size: 14px !important;
  }

  .chatSection .userChat img {
    width: 50px !important;
  }

  .chatSection .mobileFlex_reverse {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center !important;
    align-items: center !important;
  }

  .mobileFlex_Col {
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
  }

  .profileContent {
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
  }

  .userChat .chatCount {
    background-color: #000071;
    padding: 0 7px;
    font-size: 13px !important;
  }

  .userChat .mobileFlex_between {
    display: flex !important;
    justify-content: space-between !important;
  }
}

@media screen and (max-width: 320px) {
  .chatContent .miniFlex_Col {
    display: flex;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
  }
}

/* Slots Module */

.slotsSection .form-check-input:checked[type="radio"] {
  background-image: none !important;
  padding: 3px !important;
}

.slotsSection .slick-list {
  margin: 0 35px !important;
}

.slotsSection .slick-arrow {
  width: 30px !important;
}

.slotsSection .slick-prev {
  left: 0 !important;
}

.slotsSection .slick-next {
  right: 0 !important;
}

.slotsSection .slick-slider svg {
  color: #000 !important;
}

.slotsSection .dateItem {
  transition: all 0.2s ease-in-out;
  width: fit-content !important;
  border-radius: 8px !important;
  padding: 7px 8px;
  width: 55px !important;
  cursor: pointer;
}

.slotsSection .dateItem:focus,
.slotsSection .dateItem .activeDate {
  background-color: #eeebff;
}

.slotsSection .dateItem:focus p {
  color: #000071 !important;
}

.slotsSection .dateItem:focus h5 {
  color: #000071 !important;
}

.slotsSection .dateItem-section {
  margin-top: 20px;
}

.slotsSection .slot_btn {
  background-color: #81d363; /*#eeebff*/
  color: #fff !important;
  font-size: 15px !important;
  font-weight: 500 !important;
  padding: 14px 8px !important;
}

.slotsSection .slot_btn:hover, .slotsSection .slot_btn:active {
  background-color: #44BC19 !important;
}
.disable__btn-hover .slot_btn:hover, .disable__btn-hover .slot_btn:active, .disable__btn-hover .slot_btn:focus {
  background-color: #81d363 !important;
}
.slotsSection .booked,
.slotsSection .booked:hover, .slotsSection .booked:focus {
  background-color: #ff6060 !important; /* #f8f8f8 */
  color: #fff !important;
}

.slotsSection .slotContainer {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 25px;
}

.slotsSection .monthSelect {
  border-radius: 3px !important;
  font-size: 18px !important;
  color: #666666 !important;
  padding: 15px 12px !important;
}

.slotsSection button {
  border: 0 !important;
}
.slotsSection .custom-date{
  padding: 14px 10px !important;
  color: #212529 !important;
}
#bookedRadio .form-check-input:checked {
  background-color: #cccccc !important;
  border-color: #cccccc !important;
}

.radioGroup .bookedSlot {
  border: 1px solid #FD2121;
  padding: 3px 5px 5px 5px;
}

.radioGroup .availableSlot {
  border: 1px solid #81d363;
  padding: 3px 5px 5px 5px;
}

.radioGroup span {
  font-weight: 600;
  font-size: 17px;
  color: #3e3e3e;
}

#availableRadio .form-check-input:checked {
  background-color: #a391ff;
}

#availableRadio .form-check-input {
  border-color: #a391ff;
}

#bookedRadio .form-check-input,
#availableRadio .form-check-input {
  width: 2em;
  height: 2em;
  margin-right: 8px;
  margin-top: 0 !important;
}

#bookedRadio .form-check-input:checked:focus,
#availableRadio .form-check-input:checked {
  box-shadow: none !important;
}

/* Modal - Add new slot */

.addSlot_content .nav-tabs {
  border-bottom: 1px solid #999999 !important;
}

.addSlot_content .nav-tabs .nav-item .active,
.addSlot_content .nav-tabs .nav-item :focus {
  background-color: transparent !important;
  border-bottom: 2px solid #000071 !important;
  font-weight: 600 !important;
  color: #000071 !important;
  font-size: 16px !important;
  border-top: 0px !important;
}

.addSlot_content .nav-tabs .nav-item :hover {
  background-color: transparent !important;
}

.addSlot_content .nav-tabs .nav-link {
  color: #b3b3b3 !important;
  border-top: 0 !important;
  border-left: 0 !important;
  border-right: 0 !important;
  font-size: 16px !important;
  font-weight: 400 !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.addSlot_content .nav-tabs .nav-link:first-child {
  margin-right: 15px !important;
}

.slot_tab .formSelect {
  height: 45px !important;
}

/* Generic for all Modal Close Buttons */
.btn-close {
  opacity: 1 !important;
}

/* Generic for all Modals */
.modal {
  display: flex !important;
  align-items: center !important;
}

.modalCheckboxes {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.modalCheckboxes .form-check-input:focus {
  box-shadow: none !important;
}

.modalCheckboxes .form-check-input:checked {
  background-color: #000071 !important;
}

.modalCheckboxes label {
  color: #8b8b8b;
  font-weight: 400;
  font-size: 16px;
  align-self: end !important;
  margin-left: 10px;
}

.slot_btn {
  position: relative;
}

.slot_btn:hover .delete-icon {
  visibility: visible;
}

.slot_btn .delete-icon {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
  background-color: white;
  visibility: hidden;
}

@media screen and (max-width: 1500px) {
  .slotsSection .slotContainer {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
  }

  .slotsSection .slot_btn {
    font-size: 14px !important;
    padding: 14px 4px !important;
  }
}

@media screen and (max-width: 1300px) {
  .slotsSection .slotContainer {
    display: grid;
