/* Complete Profile */

.profileHeader {
  position: fixed;
  top: 0;
  right: 0;
  left: 313px;
  height: 80px;
  background-color: #fff;
  box-shadow: 36px 0px 25px rgba(0, 0, 0, 0.05);
  border-bottom: 1px solid #f0f0f0;
  z-index: 1000;
}

.profileHeader .progress {
  height: 0.5rem !important;
}

.profileHeader .progress-bar {
  background-color: #44bc19 !important;
}

/*  */
.profileSidebar {
  position: fixed;
  top: 0;
  left: -300px;
  bottom: 0;
  z-index: 1032;
  height: 100%;
  width: 300px;
  box-shadow: 3px 0px 25px rgba(0, 0, 0, 0.05);
  overflow-y: auto;
  background: #fff;
  transition: transform 0.3s ease-in-out;
}

.profileSidebar.visible {
  transform: translateX(300px);
}

@media (min-width: 992px) {
  .sidebar-toggle {
    display: none;
  }

  .profileSidebar {
    left: 0;
  }
}

@media (max-width: 991px) {
  .sidebar-toggle {
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 1033;
    width: 30px;
    height: 30px;
    background: #fff;
    border: none;
    outline: none;
  }

  .profileSidebar .hamburger {
    position: absolute;
    top: 15px;
    left: 15px;
    cursor: pointer;
  }
}

/*  */
.profileSidebar li a {
  text-decoration: none;
  font-weight: 500;
  font-size: 18px;
  color: #999999;
  transition: all 0.3s ease;
}

.profileSidebar li a.active,
.profileSidebar li a:focus {
  color: #000071;
}

.profileSidebar .spanBorder {
  border: 2.2px solid #e2e2e2;
  transition: all 0.3s ease;
}

.profileSidebar li a.active .spanBorder,
.profileSidebar li a:focus .spanBorder {
  border: 2.2px solid #000071 !important;
}

